@import url(https://fonts.googleapis.com/css?family=Orbitron&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/*
	# fonts
	font-family: 'Orbitron', sans-serif; - time display
	
	# colours
	background-dark : #212121
	basic : #4287f5
	basic-dark : #182f54
*/

.App {
	width : 100vw;
	height : 100vh;
	display : flex;
	justify-content : center;
	align-items : center;
	background-color : #212121;
	flex-direction : column;
}

.timer-container {
	background-color : inherit;
	display : flex;
	justify-content : center;
	align-items : center;
}

.control-container {
	width : 30%;
	display : flex;
	justify-content : space-around;
	align-items : center;
}

.display {
	font-family : 'Orbitron', sans-serif;
	font-size : 2.4em;
	color : #4287f5;
}

.button {
	margin-top : 130px;
	z-index : 20;
	color : #4287f5;
	font-size : 3.5em;
}

.button:hover {
	cursor : pointer;
}

.CircularProgressBar {
	position:absolute;
	display : flex;
	justify-content : center;
	align-items : center;
	-webkit-transform : scale(1.5);
	        transform : scale(1.5);
	background-color : inherit;
}

.wrapper {
	position : absolute;
	width : 300px;
	height : 300px;
	-webkit-animation-name : wrapper-show;
	        animation-name : wrapper-show;
	-webkit-animation-fill-mode : forwards;
	        animation-fill-mode : forwards;
	-webkit-animation-timing-function : linear;
	        animation-timing-function : linear;
	-webkit-animation-duration : 0.1s;
	        animation-duration : 0.1s;
}

/* Set the progress bar */
.circle {
	width: 270px;
	height : 270px;
	border : 14px solid #4287f5;
	border-radius: 50%;
	position: absolute;
	clip : rect(0px,149px,298px,0px);
	z-index : 10;
	-webkit-animation-timing-function : linear;
	        animation-timing-function : linear;	
	-webkit-animation-fill-mode : forwards;	
	        animation-fill-mode : forwards;
}

.circle-background {
	width: 260px;
	height : 260px;
	border : 25px solid #182f54;
	border-radius: 50%;
	position: absolute;
	z-index : 5;
}

.start-gap {
	position : absolute;
	top  : -156px;
	width : 25px;
	height : 27px;
	background-color : inherit;
	z-index : 15;
}

.start-gap[part~=left] {
	-webkit-transform : skewX(20deg);
	        transform : skewX(20deg);
}

.start-gap[part~=right] {
	-webkit-transform : skewX(-20deg);
	        transform : skewX(-20deg);
}

.right-spin {
	-webkit-transform : rotate(180deg);
			transform : rotate(180deg);
	-webkit-animation-name : right-spin;
	        animation-name : right-spin;
}

.left-spin {
	-webkit-animation-name : left-spin;
	        animation-name : left-spin;
}

@-webkit-keyframes right-spin {
	from {
		-webkit-transform : rotate(180deg);
		        transform : rotate(180deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@keyframes right-spin {
	from {
		-webkit-transform : rotate(180deg);
		        transform : rotate(180deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@-webkit-keyframes left-spin {
	from {
		-webkit-transform : rotate(360deg);
		        transform : rotate(360deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@keyframes left-spin {
	from {
		-webkit-transform : rotate(360deg);
		        transform : rotate(360deg);
	}
	to {
		-webkit-transform : rotate(0deg);
		        transform : rotate(0deg);
	}
}

@-webkit-keyframes wrapper-show {
	to {
		clip : rect(0px,300px,300px,149px);
	}
}

@keyframes wrapper-show {
	to {
		clip : rect(0px,300px,300px,149px);
	}
}
